import React, { useState } from 'react';
import Data from '../files/data.json';
import '../styles/card.css'
import Location from '../images/location.svg'
import pruebaIMG from '../images/rectoriaUAEM.jpg'
import profileIMG from '../images/uaem.png'
const dataSchools = Data[0].schools;
function Item (props) {
    return(
    <div
        onClick={()=> window.open(props.web, "_self")}
        id={props.name}
        className='card' 
    >
        <div className='location'>
                <img alt='location pin' src={Location} className='location-pin' />
                <p className='text'>{props.location}</p>
        </div>
        <img className='banner' src={pruebaIMG} />
        <div className='info'>
            <img className='profile-image' alt={props.name+' logo'} src={profileIMG} />
            <h2>{props.name}</h2>
        </div>
        <div className='grade'>
            <div className='item-grade' >
                <p className='grade-text'>Calidad</p>
                <h4 className='grade-number'>{props.grade}</h4>
            </div>
            <div className='item-grade' >
                <p className='grade-text'>Instalaciones</p>
                <h4 className='grade-number'>{props.classrom}</h4>
            </div>
        </div>
    </div>
    );
}


const items = dataSchools.map((data)=>
    <Item 
        banner={data.bannerImg} 
        banner-alt={data.bannerAlt} 
        location={data.location} 
        name={data.name}
        key={data.id.toString()}
        profile={data.profileImg}
        grade={data.grade}
        classrom={data.classrom}
        count={data.count}
        web={data.web}
    />
);
function Schools () {
    return(<>
    {items}
    </>);
}

export default Schools;